<template>
	<div id="projectdet-page">
		<div
			class="projectdet-thumb"
			:style="{ backgroundImage: 'url('+require('../assets/img/project/'+project_item.thumb)+')' }">	
		</div>
		<div class="projectdet container">
			<h3>{{project_item.title}}</h3>
			<p>{{project_item.desc}}</p>
			<p>
				<a :href="project_item.link" class="text-white" title="View in project page" target="_blank"><i class="fas fa-paper-plane mr-2"></i> View Project</a>
			</p>
			<vue-markdown :source="content_item" class="vue-markdown">
			</vue-markdown>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import VueMarkdown from 'vue-markdown'
import '../assets/css/projectdet.css'
import prj_data from '../data/project.json'
import git_data from '../data/repos.json'

export default {
	components: { VueMarkdown },
	data(){
		return {
			project_item: {},
			is_online: false,
			content_item: ''
		}
	},
	methods:{
		initializeProject(){
			this.is_online = navigator.onLine

			let slug = this.$route.params.slug
			let show_project

			prj_data.forEach((e) => {
				if(slug == e.slug){
					show_project = e
				}
			})
			if(show_project.github_name){
				// is github public project
				if(this.is_online){
					this.getGithubData(show_project)
				} else {
					git_data.forEach((e) => {
						if(e.name == show_project.github_name){
							show_project.github = e
						}
					})
					this.project_item = show_project
				}
			} else {
				// is github private project
				this.project_item = show_project
			}
		},
		getGithubData(show_project){
			axios
			.get('https://api.github.com/users/jhonarendra/repos')
			.then(res => {
				let repos = JSON.parse(JSON.stringify(res.data))

				repos.forEach((e) => {
					if(e.name == show_project.github_name){
						show_project.github = e
					}
				})
				this.project_item = show_project

				axios
				.get('https://raw.githubusercontent.com/'+this.project_item.github.full_name+'/master/README.md')
				// .get('https://raw.githubusercontent.com/jhonarendra/android-restoran/master/README.md')
				.then(res => {
					this.content_item = res.data
				})
			})
		},
		goBack(){
			window.history.back()
		}
	},
	mounted(){
		this.initializeProject()
	}
}
</script>